<template>
    <div class="upvoteNoticeBox">
        <div class="wattingBox" v-if="loading">
          <img src='data:image/gif;base64,'>
          <p>加载数据中...</p>
        </div>
        <p class="upvoteNoticeTitle">&thinsp;&thinsp; 收到的赞</p>
        <div class="upvoteNoticeInBox">
            <darker-scroll
                :item-count=pageSize
                :item-height=80
                :item-spacing=20
                :scroll-hight=740
                :scroll-width=840
                @nextPage="nextPage"
                style="margin-top:0;"
                class="scroll">
                <div class="upvoteItem" v-for="item in upvoteNoticeList" :key="item.id">
                    <div class="upvoteInfo">
                        <div class="upvoteLogoBox">
                            <img src="../../../static/img/upvote.png" class="upvoteLogo">
                        </div>
                        <p class="upvoter" @click="personalPage(item.publisher.uid)" style="color:aquamarine;">{{item.publisher.nick}}</p>
                        <p class="upvoteContent"><a :href="getContentUrl(item.title)" style="color:#03a9f4;font-size: 14px;">{{getContent(item.title)}}</a></p>
                        <p class="upvoteTime">{{new Date(item.createTime).toLocaleString()}}</p>
                    </div>
                </div>
            </darker-scroll>
        </div>
    </div>
</template>

<script>
import darkerScroll from "../darkerStyleComponents/darkerScroll.vue";
import api from "../../api.js";
export default {
  components: {
    darkerScroll
  },
  data() {
    return {
      loading: false,
      upvoteNoticeList: [],
      pageSize: 0,
      pageNum: 1,
      totalSize: 0,
      pbParams: {
        type: 5,
        pn: 1,
        ps: 20
      }
    };
  },
  methods: {
    async initReplyMessage() {
      let rd = (await api.getMessageList({ type: 3 })).data;
      console.log("点赞信息列表", rd);
      if (rd.code === 0) {
        this.upvoteNoticeList = rd.data.messages;
        this.totalSize = rd.data.page.totalSize;
        //不满或刚好一页
        if (rd.data.page.totalSize <= 20) this.pageSize = this.totalSize;
        else this.pageSize = 20;
      } else {
        console.log("请求列表失败");
      }
      this.loading = false;
    },
    async nextPage() {
      if (Math.ceil(this.totalSize / 20) == this.pageNum) return;
      this.loading = true;
      this.pbParams.pn++;
      this.pageNum++;
      let rd = (await api.getMessageList(this.pbParams)).data;
      console.log("下一页", rd);
      if (rd.code === 0) {
        this.upvoteNoticeList = this.upvoteNoticeList.concat(rd.data.messages);
        this.pageSize += rd.data.messages.length;
      } else {
        console.log("请求下一页失败");
      }
      this.loading = false;
    },
    getContent(content) {
      return content.substring(
        2,
        content.lastIndexOf("}", content.lastIndexOf("}", content.length) - 1)
      );
    },
    getContentUrl(content) {
      return content.substring(
        content.lastIndexOf('"', content.lastIndexOf('"', content.length) - 1) +
          1,
        content.lastIndexOf('"', content.length)
      );
    },
    personalPage(uid) {
      console.log(uid);
      this.$router.push({ name: "user", params: { uid: uid } });
    }
  },
  mounted() {
    this.loading = true;
    this.initReplyMessage();
  }
};
</script>

<style scoped>
.upvoteNoticeBox {
  display: flex;
  flex-direction: column;
}
.upvoteNoticeBox a {
  text-decoration: none;
}
.upvoteNoticeTitle {
  margin: 10px auto;
  color: wheat;
  /*background: rgba(255, 255, 255, 0.3);*/
  background: rgb(53, 50, 52);
  /*border: 1px solid rgba(255, 255, 255, 0.2);*/
  height: 40px;
  width: 91%;
  line-height: 40px;
  border-radius: 3px;
  text-align: left;
}
.upvoteNoticeInBox {
  margin: auto auto;
  width: 95%;
  height: 800px;
  display: flex;
  flex-direction: column;
  /*border: 1px solid rgba(255, 255, 255, 0.2);*/
  border-radius: 3px;
}
.upvoteItem {
  /*background: rgba(32, 32, 32, 0.377);*/
  background: rgba(90, 85, 85, 0.377);
  color: white;
  border-radius: 10px;
  width: 95%;
  height: 80px;
  display: flex;
  flex-direction: row;
  margin-top: 10px;
}
.upvoteInfo {
  width: 95%;
  display: flex;
  flex-direction: row;
  margin: auto auto;
}
.upvoteLogoBox {
  background: rgba(10, 114, 10, 0.541);
  height: 40px;
  width: 40px;
  border-radius: 40px;
  display: flex;
  flex-direction: row;
  margin: auto 0;
}
.upvoteLogo {
  height: 30px;
  width: 30px;
  margin: auto;
}
.upvoter {
  margin: auto 10px;
  text-align: left;
  width: 100px;
  cursor: pointer;
}
.upvoteContent {
  width: 450px;
  font-size: 13px;
  text-align: left;
  /*超出隐藏*/
  overflow: hidden;
  /*超出不换行*/
  text-overflow: ellipsis;
  /*超出省略号显示*/
  white-space: nowrap;
}
.upvoteTime {
  margin-top: auto;
  margin-right: 0px;
  font-size: 13px;
  height: 13px;
  color: rgb(196, 196, 196);
  width: 180px;
}
.wattingBox {
  background: white;
  position: absolute;
  z-index: 300;
  width: 150px;
  height: 100px;
  display: flex;
  flex-direction: column;
  border-radius: 5px;
  margin: 250px 275px;
}
.wattingBox img {
  margin: auto auto;
  margin-bottom: 0;
}
.wattingBox p {
  margin: auto auto;
  font-weight: bold;
}
.scroll {
  margin: 30px 18px;
  width: 95%;
}
</style>



