<template>
    <div class="systemNoticeBox">
        <div class="wattingBox" v-if="loading">
          <img src='data:image/gif;base64,'>
          <p>加载数据中...</p>
        </div>
        <p class="systemNoticeTitle">&thinsp;&thinsp; 系统通知</p>
        <div class="systemNoticeInBox">
            <darker-scroll
                :item-count=pageSize
                :item-height=80
                :item-spacing=20
                :scroll-hight=740
                :scroll-width=840
                @nextPage="nextPage"
                style="margin-top:0;"
                class="scroll">
                <div class="systemMsgItem" v-for="item in systemNoticeList" :key="item.id">
                    <div class="systemInfo">
                        <p class='systemTitle'>{{item.title}}</p>
                        <p class='systemTime'>{{new Date(item.createTime).toLocaleString()}}</p>
                    </div>
                    <p class="systemContent" v-html="item.content"></p>
                </div>
            </darker-scroll>
        </div>
    </div>
</template>

<script>
import darkerScroll from "../darkerStyleComponents/darkerScroll.vue";
import api from "../../api.js";
export default {
  components: {
    darkerScroll
  },
  data() {
    return {
      loading:false,
      systemNoticeList: [],
      pageSize: 0,
      pageNum: 1,
      totalSize: 0,
      pbParams: {
        type:4,
        pn: 1,
        ps: 20
      },
    };
  },
  methods: {
    async initMessage() {
      let rd = (await api.getMessageList({
        type: 4
      })).data;
      console.log("系统信息列表", rd);
      if (rd.code === 0) {
        this.systemNoticeList = rd.data.messages;
        this.totalSize = rd.data.page.totalSize;
        //不满或刚好一页
        if (rd.data.page.totalSize <= 20) this.pageSize = this.totalSize;
        else this.pageSize = 20;
      } else {
        console.log("请求列表失败");
      }
      this.loading = false;
    },
    async nextPage() {
      if (Math.ceil(this.totalSize / 20) == this.pageNum) return;
      this.loading=true;
      this.pbParams.pn++;
      this.pageNum++;
      let rd = (await api.getMessageList(this.pbParams)).data;
      console.log("下一页", rd);
      if (rd.code === 0) {
        this.systemNoticeList = this.systemNoticeList.concat(rd.data.messages);
        this.pageSize += rd.data.messages.length;
      } else {
        console.log("请求下一页失败");
      }
      this.loading=false;
    }
  },
  mounted() {
    this.loading=true;
    this.initMessage();
  }
};
</script>

<style scoped>
.systemNoticeBox {
  display: flex;
  flex-direction: column;
}
.systemNoticeBox a{
  text-decoration: none;
}
.systemNoticeTitle {
  margin: 10px auto;
  color: wheat;
  background: rgb(53, 50, 52);
  /*border: 1px solid rgba(255, 255, 255, 0.2);*/
  height: 40px;
  width: 91%;
  line-height: 40px;
  border-radius: 3px;
  text-align: left;
}
.systemNoticeInBox {
  margin: auto auto;
  width: 95%;
  height: 800px;
  display: flex;
  flex-direction: column;
  /*border: 1px solid rgba(255, 255, 255, 0.2);*/
  border-radius: 3px;
}
.systemMsgItem {
  /*background: rgba(32, 32, 32, 0.377);*/
  background: rgba(90, 85, 85, 0.377);
  color: white;
  border-radius: 10px;
  width: 95%;
  height: 80px;
  display: flex;
  flex-direction: column;
  margin-top: 10px;
  text-align: left;
}
.systemInfo {
  display: flex;
  flex-direction: row;
  margin-top: 10px;
}
.systemTitle {
  margin: auto 10px;
  color: #5ec3a1;
}
.systemTime {
  margin: auto 10px;
  font-size: 13px;
  color: rgb(196, 196, 196);
}
.systemContent {
  font-size: 13px;
  width: 98%;
  margin: auto 10px;
}
.wattingBox {
  background: white;
  position: absolute;
  z-index: 300;
  width: 150px;
  height: 100px;
  display: flex;
  flex-direction: column;
  border-radius: 5px;
  margin: 250px 275px;
}
.wattingBox img {
  margin: auto auto;
  margin-bottom: 0;
}
.wattingBox p {
  margin: auto auto;
  font-weight: bold;
}
.scroll {
  margin: 30px 18px;
  width: 95%;
}
</style>
<style>
  .systemContent a{
    color: #0097d0;
    cursor: pointer;
    text-decoration: none;
  }

  .systemContent a:hover {
    color: #0bbbfd;
  }
</style>



