<template>
  <div class="commentItemBox" :id="rootReply.rpid">
    <div class="commentItemInBox">
      <a :href="'#/user/'+rootReply.uid" target="_blank">
        <img :src="rootReply.user.avatar?rootReply.user.avatar:'../../../static/img/noAvatar.jpg'" class="commentItemAvatar">
      </a>
      <div class="commentContentBox">
        <div class="replyUsername">
          <a :href="'#/user/'+rootReply.uid" target="_blank" style="text-decoration: none;color: #D1EEEE">
            {{rootReply.user.nick}}
          </a>
          <img v-if="top" class="stickFlag" src="../../../static/img/stick.png">
          <div class="adminBoxOutBox" tabindex="1" @blur="showAdminBox=false">
            <img class="adminMenuBtn" src="../../../static/img/replyMenu.png" @click="showAdminBox=!showAdminBox">
              <transition name="adminBoxTran">
                <div class="adminBox" v-if="showAdminBox">
                  <div class="adminInBox">
                    <p :class="['stick','greyBtn']" v-if="!top && canSetTop" @click="stickReply(rootReply.rpid)">置顶</p>
                    <p :class="['stick','greyBtn']" class="stick" v-if="top && canSetTop" @click="unStickReply(rootReply.rpid)">取消置顶</p>
                    <p @click="clickReport(1,rootReply.rpid)" v-if="canReport" class="report">举报</p>
                    <p class="delete" v-if="canDelReply" @click="showDelDialog">删除</p>
                  </div>
                </div>
              </transition>
          </div>
        </div>
        <p class="replyContent" v-html="replyContent"></p>
        <div class="commentControlBox">
          <p class="commentFloor">#{{rootReply.floor}}</p>
          <p class="commentTime">{{getDateDiff}}</p>
          <div class="commentUpvote" @click="upvote">
            <img :src="rootReply.like_status===0?'../../../static/img/upvote2.png':'../../../static/img/upvoted2.png'">
            <p>{{rootReply.like}}</p>
          </div>
          <p class="commentReplyBtn" @click="toggleReplyBox">回复</p>
          <div class="delConfirmOutBox" tabindex="1" @blur="showDelBox=false">
            <transition name="delConfirmTran">
              <div class="deleteConfirm" v-if="showDelBox">
                <p class="deleteConfirmTip">确定删除吗？</p>
                <div class="confirmDeleBtnBox">
                  <p class="cancelDelBtn" @click="showDelBox=!showDelBox">取消</p>
                  <p class="confirmDelBtn" @click="delRootReply">确定</p>
                </div>
              </div>
            </transition>
          </div>
        </div>
      </div>
    </div>
    <div v-for="(subReply,index) in rootReply.replies" :key="subReply.rpid">
      <sub-reply :subReply="subReply"
                 :subIndex="index"
                 :oid="oid"
                 :type="type"
                 @onRecvSubReport="clickReport"
                 @onRecvShowLoginBox="showLoginBox = true"
                 @onRemoveSubReply="removeSubReply"
                 @onShowSubReplyBox="showSubReplyBox">
      </sub-reply>
    </div>
    <div v-if="rootReply.rcount>3 && !page" style="text-align: left;margin-bottom: 10px;">
      <span style="color: #c5c8c6;font-size: 13px;">共{{rootReply.rcount}}条回复，</span>
      <span @click="seeMore" v-show="!noMore"  class="seeMore">点击查看更多</span>
      <!--<div @click="seeMore" v-show="!noMore" v-if="rootReply.rcount>3 && !page" class="seeMore">点击查看更多</div>-->
    </div>

    <div v-if="noMore">
      <el-pagination
        class="sub-pgn"
        v-show="noMore"
        v-if="page.totalSize>10"
        @current-change="pageSelect"
        :small="true"
        :current-page.sync="page.pageNumber"
        :page-count=11
        :page-size="page.pageSize"
        layout="total, prev, pager, next"
        :total="page.totalSize"
        style="margin-left:45px;">
      </el-pagination>
    </div>
    <post-reply
      :oid="oid"
      :type="type"
      v-show="showReplyBox"
      class="commentReplyBox"
      :replyInfo="replyInfo"
      :parentRpid="rootReply.rpid"
      :mode=1
      :top="top"
      :hot="hot"
      @onAddHotSubReply="addHotSubReply"
      @onAddTopSubReply="addTopSubReply"
      @onAddSubReply="addSubReply">
    </post-reply>

    <el-dialog title="举报" width="400" :visible.sync="showReportBox" append-to-body>
      <el-radio-group v-model="reportType">
        <div>
          <el-radio :label=1>辱骂</el-radio>
          <el-radio :label=2>色情</el-radio>
          <el-radio :label=3>垃圾广告</el-radio>
          <el-radio :label=4>引战</el-radio>
        </div>
        <div style="margin-top: 20px">
          <el-radio :label=5>剧透</el-radio>
          <el-radio :label=6>人身攻击</el-radio>
          <el-radio :label=7>隐私侵犯</el-radio>
          <el-radio :label=8>刷屏</el-radio>
        </div>
        <div style="margin-top: 20px">
          <el-radio :label=9>违法违规</el-radio>
          <el-radio :label=10>低俗</el-radio>
          <el-radio :label=11>赌博诈骗</el-radio>
          <el-radio :label=12>其他</el-radio>
        </div>
      </el-radio-group>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancelReport">取消</el-button>
        <el-button :disabled="reportType === ''" type="primary" @click="handleRootReplyReoprt">确定</el-button>
      </div>
    </el-dialog>

    <transition name="loginNowBoxTran">
      <login-box class="loginNowBox" v-if="showLoginBox" @closeLoginBox="showLoginBox=false"></login-box>
    </transition>

  </div>
</template>

<script>
import PostReply from "./PostReply.vue";
import SubReply from "./SubReply.vue";
import api from "../../api.js";
import { formatDate } from "../../time.js";
import loginBox from "../login/login.vue";
export default {
  name: "RootReply",
  props: ["rootReply", "rootIndex", "oid", "type", "rpid", "subPage", "refresh", "top", "hot"],
  components: {
    "post-reply": PostReply,
    "sub-reply": SubReply,
    "login-box": loginBox
  },
  data() {
    return {
      uid: "",
      role: "",
      page: "",
      showReplyBox: false,
      noMore: false,
      replyInfo: "",
      placeholder: "",
      showDelBox: false,
      showAdminBox: false,
      showReportBox: false,
      showLoginBox: false,
      reportType: "",
      rtt: "",
      tid: ""
    };
  },
  watch: {
    refresh(val){
      console.log("refresh!!!",val);
      this.page = "";
      this.noMore = false
    }
  },
  computed: {
    getDateDiff() {
      let minute = 1000 * 60;
      let hour = minute * 60;
      let day = hour * 24;
      let now = new Date().getTime();
      let diffValue = now - this.rootReply.ctime;
      if (diffValue < 0) {
        return;
      }
      let dayC = diffValue / day;
      let hourC = diffValue / hour;
      let minC = diffValue / minute;
      if (dayC >= 1) {
        return formatDate(new Date(this.rootReply.ctime), "yyyy-MM-dd hh:mm");
      } else if (hourC >= 1) {
        return "" + parseInt(hourC) + "小时前";
      } else if (minC >= 1) {
        return "" + parseInt(minC) + "分钟前";
      } else return "刚刚";
    },
    canDelReply() {
      if (!this.uid) return false; //没有登陆，不能删除评论
      if (this.type === 4) {
        //用户资料下的评论
        if (this.uid === this.oid) {
          //在自己的资料下面，可以删除全部评论，oid为要查看的用户资料的uid
          return true;
        } else if (this.role) {
          //管理员可以删除全部评论
          if (this.role === "ROLE_ADMIN" || this.role === "ROLE_MANAGER")
            return true;
        } else if (this.uid === this.rootReply.uid) {
          //用户可以删除自己发的评论
          return true;
        }
      } else {
        //其他类型的评论
        if (this.uid === this.rootReply.uid) {
          //用户可以删除自己发的评论
          return true;
        } else if (this.role) {
          //管理员
          if (this.role === "ROLE_ADMIN" || this.role === "ROLE_MANAGER")
            return true;
        }
      }
      return false;
    },
    canSetTop() {
      if (!this.uid)
        //没有登陆
        return false;
      if (this.type === 4) {
        //用户资料下的评论
        if (this.uid === this.oid) {
          //自己的资料下面的评论，可以进行置顶
          return true;
        } else {
          if (this.role === "ROLE_ADMIN" || this.role === "ROLE_MANAGER")
            return true; //管理员为所欲为
        }
      } else {
        //其他类型的评论
        if (this.role === "ROLE_ADMIN" || this.role === "ROLE_MANAGER")
          return true; //其他类型的评论一般只有管理员可以置顶
      }
      return false;
    },
    canReport(){
      if(this.uid == this.rootReply.uid)
        return false;
      return true;
    },
    replyContent() {
      //替换所有的换行符
      let string = this.rootReply.content;
      string = string.replace(/\r\n/g, "<br>");
      string = string.replace(/\n/g, "<br>");
      //替换所有的空格（中文空格、英文空格都会被替换）
      string = string.replace(/\s/g, "&nbsp;");
      //   console.log("replaced content:",string);
      return string;
    }
  },
  methods: {
    cancelReport(){
      this.showReportBox = false;
      this.reportType = "";
    },
    clickReport(rtt,tid){
      if(!this.uid){
        this.showLoginBox = true;
        return;
      }
      this.rtt = rtt;
      this.tid = tid;
      this.showReportBox = true;
    },
    async handleRootReplyReoprt(){
      if(!this.uid){
        console.log("show Login Box");
        this.showLoginBox = true;
        return;
      }
      if(!this.reportType){
        console.log("no type selcted");
        return;
      }
      let res = await api.postReport({
        rtt: this.rtt,
        rt: this.reportType,
        tid: this.tid
      });
      let rd = res.data;
      if(rd.code === 0){
        this.$message({
          message: "举报成功",
          type: "success"
        });
        this.reportType = "";
      }
      else {
        this.$notify({
          title: '',
          message: rd.msg,
          type: 'warning'
        });
      }
      this.showReportBox = false;
    },
    showDelDialog() {
      this.showAdminBox = false;
      this.showDelBox = !this.showDelBox;
    },
    async seeMore() {
      this.noMore = true;
      let res = await api.getRepliesOfAnyClassPage({
        pn: 1,
        oid: this.oid,
        type: this.type,
        root: this.rootReply.rpid
      });
      let rd = res.data;
      console.log("view more subReplies:", rd);
      if (rd.code === 0) {
        // this.rootReply.replies = rd.data.replies;
        this.rootReply.replies.splice(0);
        let i = 0;
        for (i = 0; i < rd.data.replies.length; i++) {
          this.rootReply.replies.push(rd.data.replies[i]);
        }
        this.page = rd.data.page;
        this.noMore = true;
      } else {
        console.log("查看更多子评论失败");
      }
    },
    async pageSelect(index) {
      let res = await api.getRepliesOfAnyClassPage({
        pn: index,
        oid: this.oid,
        type: this.type,
        root: this.rootReply.rpid
      });
      let rd = res.data;
      if (rd.code === 0) {
        this.rootReply.replies.splice(0);
        let i = 0;
        for (i = 0; i < rd.data.replies.length; i++) {
          this.rootReply.replies.push(rd.data.replies[i]);
        }
        // this.rootReply.replies = rd.data.replies;
        this.page = rd.data.page;
      } else {
        console.log("查看更多子评论失败");
      }
    },
    async upvote() {
      let upvoteRes;
      if (this.rootReply.like_status === 0) {
        upvoteRes = await api.upvoteReply(this.rootReply.rpid);
        this.rootReply.like++;
        this.rootReply.like_status = 1;
      } else {
        upvoteRes = await api.cancelUpvoteReply(this.rootReply.rpid);
        this.rootReply.like--;
        this.rootReply.like_status = 0;
      }
    },
    async delRootReply() {
      let res = await api.deleteMyRpely(this.rootReply.rpid);
      let rd = res.data;
      if (rd.code === 0) {
        if (this.top) {
          this.$emit("onRemoveTopReply");
          this.page = "";
          this.noMore = false;
        } else {
          this.$emit("onRemoveRootReply", this.rootIndex);
        }
        this.$message({
          message: "删除成功",
          type: "success"
        });
      } else {
        console.log("删除根评论失败，index:", this.rootIndex);
      }
    },
    removeSubReply(index) {
      console.log("remove sub reply: ", index);
      this.rootReply.replies.splice(index, 1);
    },
    showSubReplyBox(subRpy) {
      this.replyInfo = subRpy;
      this.showReplyBox = true;
    },
    toggleReplyBox() {
      this.replyInfo = this.rootReply;
      this.showReplyBox = !this.showReplyBox;
    },
    async addSubReply(rpid) {
      let res = await api.getRepliesOfAnyClassPage({
        rpid: rpid,
        oid: this.oid,
        type: this.type
        // root: this.rootReply.rpid
      });
      let rd = res.data;
      if (rd.code === 0) {
        this.$emit("onUpdateRootReply", rd.data);
        this.page = rd.data.subpage;
        this.showReplyBox = false;
        this.noMore = true;
      } else {
        console.log("添加评论后刷新子评论失败");
      }
    },
    async addTopSubReply(rpid) {
      let res = await api.getRepliesOfAnyClassPage({
        rpid: rpid,
        oid: this.oid,
        type: this.type
        // root: this.rootReply.rpid
      });
      let rd = res.data;
      if (rd.code === 0) {
        this.$emit("onUpdateTopReply", rd.data);
        this.page = rd.data.subpage;
        this.showReplyBox = false;
        this.noMore = true;
      } else {
        console.log("添加评论后刷新置顶子评论失败");
      }
    },
    async addHotSubReply(rpid) {
      let res = await api.getRepliesOfAnyClassPage({
        rpid: rpid,
        oid: this.oid,
        type: this.type
        // root: this.rootReply.rpid
      });
      let rd = res.data;
      if (rd.code === 0) {
        this.$emit("onUpdateHotReply", rd.data, this.rootIndex);
        console.log("刷新热评：",rd.data);
        this.page = rd.data.subpage;
        this.showReplyBox = false;
        this.noMore = true;
      } else {
        console.log("添加评论后刷新热评失败");
      }
    },
    async stickReply(rpid) {
      console.log("置顶rpid", rpid);
      let resData = (await api.stickReply(rpid)).data;
      console.log("置顶", resData);
      if (resData.code === 0) {
        //初始化列表
        this.$emit("onRefreshComment");
        this.$message({
          message: "置顶成功",
          type: "success"
        });
      } else {
        this.$message({
          message: message.msg,
          type: "error"
        });
      }
    },
    async unStickReply(rpid) {
      console.log("取消置顶rpid", rpid);
      let resData = (await api.unstickReply(rpid)).data;
      console.log("取消置顶", resData);
      if (resData.code === 0) {
        //初始化列表
        this.$emit("onRefreshComment");
        this.$message({
          message: "取消置顶成功",
          type: "success"
        });
      } else {
        this.$message({
          message: message.msg,
          type: "error"
        });
      }
    }
  },
  created() {
    let id = localStorage.getItem("USER_ID");
    let rol = localStorage.getItem("ROLE");
    if (id) {
      this.uid = parseInt(id);
    }
    if (rol) {
      this.role = rol;
    }
    if (this.subPage) {
      if (this.rootReply.rpid === this.subPage.rootId) {
        this.page = this.subPage;
        this.noMore = true;
      }
    }
  }
};
</script>


<style scoped>
.commentItemBox {
  display: flex;
  flex-direction: column;
  width: 900px;
  margin: 10px auto;
  border-bottom: 1px solid #504141;
}
.commentItemInBox {
  display: flex;
  flex-direction: row;
}
.commentItemAvatar {
  height: 45px;
  width: 45px;
  border-radius: 50px;
  cursor: pointer;
  margin: 12px 10px 12px auto;
}
.commentContentBox {
  margin: auto auto;
  text-align: left;
  position: relative;
}
.commentControlBox {
  display: flex;
  flex-direction: row;
}
.replyUsername {
  text-align: left;
  margin: 10px 10px;
  font-weight: bold;
  color: rgb(127, 162, 238);
  /*内容自适应宽度*/
  display: flex;
  flex-direction: row;
}
.adminMenuBtn {
  width: 20px;
  height: 20px;
  margin: auto auto;
  margin-right: 0;
  cursor: pointer;
}
.stickFlag {
  width: 35px;
  height: 20px;
  margin-left: 5px;
}
.replyContent {
  color: #c5c8c6;
  height: auto;
  text-align: left;
  margin: -8px 10px;
  width: 820px;
  word-wrap: break-word;
  word-break: break-all;
}
.commentControlBox {
  width: 820px;
  height: 20px;
  margin: 12px 10px;
}
.commentFloor {
  font-size: 12px;
  margin: auto 0;
  color: gray;
}
.commentTime {
  font-size: 12px;
  margin: auto 10px;
  color: gray;
}
.commentUpvote {
  display: flex;
  flex-direction: row;
  margin: auto 5px;
  cursor: pointer;
}
.commentUpvote img {
  width: 17px;
  height: 17px;
  margin: auto auto;
}
.commentUpvote p {
  font-size: 12px;
  margin: auto auto;
  color: gray;
  padding-left: 3px;
}
.commentReplyBtn {
  margin: auto 10px;
  color: rgb(87, 117, 146);
}
.commentDeleteBtn {
  margin: auto auto;
  margin-right: 0;
  color: rgb(156, 4, 4);
}
.commentDeleteBtn,
.commentReplyBtn {
  font-size: 13px;
  cursor: pointer;
  font-weight: bold;
}
.commentReplyBox {
  margin: auto auto;
  margin-bottom: 10px;
}
.seeMore {
  color: rgb(67, 160, 183);
  font-size: 13px;
  /*font-weight: bold;*/
  /*margin-bottom: 10px;*/
  text-align: left;
  /*margin-left: 50px;*/
  cursor: pointer;
}
.childRepliesPageBox {
  display: flex;
  flex-direction: row;
  background: salmon;
}
.deleteConfirm {
  position: absolute;
  right: -5%;
  display: flex;
  flex-direction: column;
  background: white;
  width: 150px;
  height: 70px;
  margin-top: -90px;
  border-radius: 5px;
  overflow: hidden;
}
.delConfirmTran-leave-active,
.delConfirmTran-enter-active {
  transition: all 0.2s ease;
}
.delConfirmTran-leave-active,
.delConfirmTran-enter {
  height: 0px !important;
  /*!important 将该样式优先级调至最高*/
  opacity: 0;
}
.delConfirmTran-leave,
.delConfirmTran-enter-active {
  height: 70px;
}
.deleteConfirmTip {
  width: 100%;
  margin: 10px auto;
  color: red;
  font-weight: bold;
  font-size: 13px;
  text-align: center;
}
.confirmDeleBtnBox {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: -10px auto;
  margin-bottom: 0;
}
.confirmDeleBtnBox p {
  width: 50px;
  text-align: center;
  font-size: 12px;
  height: 20px;
  line-height: 20px;
  cursor: pointer;
  color: white;
}
.cancelDelBtn {
  background: gray;
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
}
.confirmDelBtn {
  background: rgb(255, 73, 73);
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
}
.adminBox {
  width: 70px;
  background: white;
  position: absolute;
  right: -2%;
  margin-top: 0px;
  display: flex;
  flex-direction: column;
  border-radius: 3px;
  /*overflow: hidden;*/
  outline: none;
  z-index: 30;
}
.adminInBox {
  margin: auto auto;
  width: 80%;
  overflow: hidden;
}
.adminBoxTran-leave-active,
.adminBoxTran-enter-active {
  transition: all 0.2s ease;
}
.adminBoxTran-leave-active,
.adminBoxTran-enter {
  height: 0px !important;
  /*!important 将该样式优先级调至最高*/
  opacity: 0;
}
.adminBoxTran-leave,
.adminBoxTran-enter-active {
  height: 70px;
}
.adminBox p {
  text-align: center;
  margin: 10px auto;
  font-size: 12px;
  line-height: 20px;
  border-radius: 3px;
  color: white;
  cursor: pointer;
  display: block;
}
.stick {
  background: rgb(79, 199, 255);
}
.report {
  background: rgb(66, 89, 125);
}
.delete {
  background: rgb(255, 73, 73);
}
.greyBtn {
  background: rgb(168, 168, 168);
}
.adminBoxOutBox {
  margin: auto auto;
  margin-right: 0;
  outline: none;
}
.delConfirmOutBox {
  outline: none;
  margin: auto auto;
  margin-right: 0;
}
.loginNowBox {
  position: fixed;
  z-index: 1000;
  top: 60px;
  left: 0;
  opacity: 1;
}
.loginNowBoxTran-enter-active,
.loginNowBoxTran-leave-active {
  transition: opacity 0.5s;
}
.loginNowBoxTran-enter, .loginNowBoxTran-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
</style>
<style>
  .sub-pgn {
    text-align: left;
  }
  .sub-pgn .el-pagination__total {
    color: wheat;
  }
  .sub-pgn .el-pager li {
    background-color: transparent !important;
    color: #606266 !important;
  }
  .sub-pgn .btn-prev, .sub-pgn .btn-next {
    background-color: transparent !important;
  }
  .sub-pgn .el-pager .active {
    color: #fff !important;
  }
</style>



